/*Small Laptops*/
@media screen
and (min-width: 800px) and (max-width: 1023px)
and (min-height: 600px) and (max-height: 768px){
    .navigator-tabs .tab { font-size:.5em; }
    .ontologyList li {
        font-size: .9em;
    }
}
/*Average Laptops*/
@media screen
and (min-width: 1024px)
and (min-height: 768px){
    .navigator-tabs .tab { font-size:1em; }
    .ontologyList li {
        font-size: 1.1em;
    }
}

.NavigatorComponent {
    background-color: #F2F2F2;
    margin-top: 10%;
}

.navigator-tabs .tab {
    cursor: pointer;
    display: block;
    background-color: #7F7F7F;
    padding: 1%;
    font-weight: bold;
    text-align: center;
}

.navigator-options-wrapper {
    margin: 2%;
    border: 1px solid #d2d2d2;
}

.dataNavView, .TINavView {
    padding: 6% 0 0;
    display: none;
}

.dataNavView.active, .TINavView.active {
    display: block;
    background-color: #F2F2F2;
}

.tab.dataNavView.active, .tab.TINavView.active {background-color: #F2F2F2;}

.ontology {cursor: pointer;}

.ontologyList {
    list-style: none;
    margin: 0;
    padding: 0;
}

.ontologyList li {
    background: url('indice-icon.png')no-repeat left top;
    height: 1.5em;
    padding-left: 2.2em;
    cursor: pointer;
    margin-bottom: 1%;
}

li.Stories.ontology {
    background: url('data:image/png;base64,') no-repeat left top;
}

li.People.ontology {
    background: url('data:image/png;base64,') no-repeat left top;
}

li.Places.ontology {
    background: url('data:image/png;base64,') no-repeat left top;
}

li.Fieldtrips.ontology {
    background: url('data:image/png;base64,') no-repeat left top;
}

li:hover.ontology {
    background-color: lightgray;
}

li.ontology.active {
    background-color: #909090;
}

.NavigationDropdownMenu {
    margin: 2%;
}
