    background-color: #ffffff;
}

.closeTabIcon, .pinTabIcon{
    position: absolute;
}

.closeTabIcon {
    top: 30%;
    width: .75em;
    right: 5px;
}
.pinTabIcon{
    top: 25%;
    width: 1em;
    left: 7px;
}

.closeTabIcon:hover {
    border: 1px solid black;
    border-radius: 100%;
}

.associated-items {
    cursor: pointer;
}

.pdfViewer .mgrpdf__wrapper canvas {
    height: 87vh!important;
}

/* indicator that pops up to show the potential result of a drag */
#dragIndicator {
    /* it should be positioned independently of everything else */
    position: absolute;
    /* shouldn't be too wide or too thin */
    width: 2px;
    /* make it PURPLE */
    background-color: purple;
}

/*Small Laptops*/
@media screen
and (min-width: 800px) and (max-width: 1023px)
and (min-height: 600px) and (max-height: 700px){
    .heading-stuff{font-size:.8em;}
    .heading-stuff .flag img{padding-top:10px;}
    .heading-stuff .danish-folklore{padding-top:6px;}
    .Menu .list .menu-item{font-size: .7em;}
}
/*Average Laptops*/
@media screen
and (min-width: 1024px)
and (min-height: 700px) and (max-height:768px){
    .heading-stuff{font-size:1.15em;}
    .heading-stuff .flag img{padding-top:10px;height:50px;}
    .heading-stuff .danish-folklore{padding-top:6px;}
    .Menu .list .menu-item{font-size: .9em;}
}

/*Tall Laptops*/
@media screen
    and(min-height:769px){
    .Menu .list{font-size:1.5em;}
}

