.view{
    background-color: #f5f5f5;
}

.tabs {
    list-style: none;
    background-color: white;
    padding-bottom: 100px;
}

.tabs li {
    position: relative;
    cursor: pointer;
    display: inline;
    padding: .4vw 2vw .4vw 2.2vw;
    margin: 1px;
    background-color: #dddddd;
    border-radius: 0 0 5px 5px;
    border: 1px solid #909090;
    font-size: 1em;
}

.tabs li.active {
    background-color: #ffffff;
}

.closeTabIcon, .pinTabIcon{
    position: absolute;
}

.closeTabIcon {
    top: 30%;
    width: .75em;
    right: 5px;
}
.pinTabIcon{
    top: 25%;
    width: 1em;
    left: 7px;
}

.closeTabIcon:hover {
    border: 1px solid black;
    border-radius: 100%;
}

.associated-items {
    cursor: pointer;
}

.pdfViewer .mgrpdf__wrapper canvas {
    height: 87vh!important;
}

/* indicator that pops up to show the potential result of a drag */
#dragIndicator {
    /* it should be positioned independently of everything else */
    position: absolute;
    /* shouldn't be too wide or too thin */
    width: 2px;
    /* make it PURPLE */
    background-color: purple;
}
